.html {
  width: 100%;

  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  font-weight: 400;
  font-style: normal;

  margin: 0;
  box-sizing: border-box;
}

.body {
  color: $main;

  display: grid;
  grid-template-rows: max-content 1fr max-content;

  margin: 0 auto;
}

.body--disable-scroll {
  width: 100%;
  height: 100vh;

  position: fixed;
  left: 0;
  top: 0;

  overflow: hidden;
}

.main {
  grid-row: 2 / 3;

  background: url("../img/main-map.svg") no-repeat 238px 2600px;
  background-size: 1443.66px 1088.9px;

  margin: 0 auto;

  @include tablet {
    grid-row: 2 / 3;

    background: none;

    margin: 0;
  }

  @include mobile {
    grid-row: 2 / 3;

    background: none;

    margin: 0;
  }
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;

  border: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;

  padding: 0;
  margin: -1px;
}
